.footer__container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 2fr;
  padding: 0 3rem;
  max-width: 1905px;
  width: 100%;
  margin: 0 auto;
  margin-bottom: 3rem;
  grid-template-areas: 
    "button nav contact text";
  column-gap: 10rem;
  container-type: inline-size;
}

.footer__container-btn {
  font-size: 3.5rem;
  font-weight: 400;
  line-height: 1.25;
  color: #feffb3;
  margin: 0;
  align-self: center;
  background: transparent;
  border: none;
  cursor: pointer;
  grid-area: button;
}

.footer__container-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
}

.footer__container-navTitle {
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 1.5rem;
  letter-spacing: 0.25rem;
  color: #feffb3;
  padding-bottom: 0.5rem;
}

.footer__container-navBtn {
  color: #fbe0f3;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5rem;
  letter-spacing: 0.25rem;
  background: transparent;
  border: none;
  text-align: start;
  padding: 0;
  transition: all 0.15s ease-in-out;
  cursor: pointer;
}

.footer__container-contactsTitle {
  color: #feffb3;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 1.5rem;
  letter-spacing: 0.25rem;
  padding-bottom: 1.5rem;
}

.footer__container-contactsIcons {
  display: flex;
  gap: 2rem;
  height: 2rem;
  width: 2rem;
  border-radius: 50%;
}

.footer__container-contactsBtn {
  background: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
  display: inline-block;
  border-radius: 50%;
}

.footer__container-contacts {
  display: flex;
  flex-direction: column;
}

.footer__container-policy {
  background: transparent;
  border: none;
  padding: 0;
  color: rgba(251, 224, 243, 0.8);
  font-size: 1rem;
  font-weight: 400;
  line-height: 1rem;
  cursor: pointer;
  text-align: start;
  transition: all .25s ease-out;
}

.footer__container-navBtn:hover {
  color: #e18186;
}

.footer__container-variation {
  display: flex;
  flex-direction: column;
  align-self: center;
  gap: 2rem;
  grid-area: text;
  justify-self: end;
}

.footer__container-variation p {
  font-size: 1rem;
  font-weight: 400;
  line-height: 1rem;
  color: #FBE0F3;
}

.footer__container-variation-buttons {
  display: flex;
  flex-direction: column;
  text-align: start;
  gap: 1rem;
}

.footer__container-variation-paragraph {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  color: #FBE0F3;
}

.variation-modal {
  align-self: center;
}

@media (max-width: 768px) { 
  .footer__container-variation {
    width: 100%;
    justify-self: stretch;
  }

  .footer__container-btn {
    font-size: 12vw;
    line-height: 1;
    text-align: center;
  }

  .footer__container-navTitle {
    font-size: 1rem;
    line-height: 1rem;
    letter-spacing: 2px;
    padding-bottom: 0;
    font-weight: 400;
  }

  .footer__container-navBtn {
    font-size: .75rem;
    line-height: .75rem;
    letter-spacing: 2px;
    text-align: center;
  }

  .footer__container-nav {
    grid-area: nav;
    padding: 0;
    width: fit-content;
    justify-self: end;
    gap: 1rem;
  }

  .footer__container-contactsTitle {
    font-size: 1rem;
    font-weight: 400;
    line-height: 1rem;
    letter-spacing: 2px;
    padding-bottom: 1rem;
  }

  .footer__container-contactsBtn img, .footer__container-contactsBtn {
    max-width: 1.25rem;
    height: 1.25rem;
  }

  .footer__container-contactsIcons {
    display: flex;
    gap: 1rem;
    width: auto;
  }

  .footer__container-contacts {
    grid-area: contact;
    padding: 0;
    margin: 0;
  }

  .footer__container {
    padding: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 4rem;
    column-gap: 4rem;
    max-width: none;
    grid-template-areas: 
    "nav contact"
    "text text"
    "button button";
    margin-bottom: 3rem;
  }

  .footer__container-variation {
    gap: 1rem;
  }

  .footer__container-variation-buttons button, .footer__container-variation p {
    font-size: .75rem;
    text-align: center;
    line-height: .75rem;
  }
}