.cart-wrapper {
  position: fixed;
  left: 0;
  top: 0;
  background: transparent;
  height: 100%;
  width: 100%;
  z-index: 9999;
  display: flex;
  justify-content: end;
  overflow-y: scroll;
  box-sizing: border-box;
  align-items: flex-start;
  color-scheme: dark;
  opacity: 1;
  transition: all .5s ease-out;
}

.background {
  position: fixed;
  left: 0;
  top: 0;
  background-color: rgba(16, 16, 16, 0.8);
  height: 100%;
  width: 100%;
  z-index: 9998;
  transition: opacity .3s ease-out, visibility .3s ease-out;
  opacity: 1;
  visibility: visible;
}

.invisible {
  transform: translateX(100%);
}

.invisible_bg {
  opacity: 0;
  visibility: hidden;
}


.cart-container {
  background-image: url("/public/images/pattern.webp");
  background-repeat: repeat;
  background-size: 600px 600px;
  image-rendering: pixelated;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 58.5rem;
  position: relative;
}

.exit-btn {
  background: none;
  border: 0;
  padding: 0;
  cursor: pointer;
  width: fit-content;
  margin: 1.75rem 3rem 1.75rem auto;
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.title {
  font-size: 2rem;
  font-weight: 400;
  line-height: 2rem;
  margin-top: 0.5rem;
  text-align: start;
}

.card-container {
  display: flex;
  padding: 1rem;
  border: 2px solid #fbe0f3;
  margin: 0 3rem 4rem 3rem;
  min-width: 52.5rem;
}

.img {
  margin-right: 1.5rem;
  max-width: 8rem;
  max-height: 11.5rem;
}

.delete-btn {
  padding: 0;
  margin: 0;
  border: 0;
  background: none;
  cursor: pointer;
}

.price {
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 1.5rem;
  color: #feffb3;
  letter-spacing: 4px;
  text-decoration: underline;
  text-wrap: nowrap;
}

.utils {
  margin-left: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: end;
}

.last-price-container {
  margin-left: 3rem;
  display: flex;
  flex-direction: column;
  color: #feffb3;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1rem;
  letter-spacing: 4px;
  gap: 0.5rem;
}

.last-price {
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 1.5rem;
  color: #feffb3;
  text-decoration: underline;
}

.buy-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: auto 3rem 5rem 0;
  padding-top: 2rem;
}

.buy-btn {
  background: none;
  border: 2px solid #fbe0f3;
  border-radius: 20rem;
  padding: 1rem 10rem 1rem 10rem;
  display: flex;
  align-items: center;
  gap: 1.5rem;
  cursor: pointer;
  font-size: 1.5rem;
  color: #fbe0f3;
  font-weight: 400;
  letter-spacing: 4px;
  line-height: 1.5rem;
}

.buy-btn svg {
  max-width: 1.5rem;
  max-height: 1.5rem;
}

.cart-nothing-title {
  font-size: 2rem;
  font-weight: 400;
  line-height: 2rem;
  text-align: start;
  color: #fbe0f3;
}

.paragraph-pc, .paragraph-mobile {
  font-size: 1.5rem;
  line-height: 1.5rem;
  font-weight: 400;
  letter-spacing: 4px;
  color: #fbe0f3;
  text-align: start;
}

.paragraph-mobile {
  display: none;
  font-size: 1rem;
  line-height: 1rem;
  letter-spacing: 0;
}

.cart-nothing-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: -1rem 0 0 3rem;
  flex-grow: 1;
}

.catalog-btn {
  background: none;
  border: 2px solid #fbe0f3;
  border-radius: 20rem;
  padding: 1rem 3rem 1rem 3rem;
  display: flex;
  align-items: center;
  gap: 1.5rem;
  cursor: pointer;
  font-size: 1.5rem;
  color: #fbe0f3;
  font-weight: 400;
  letter-spacing: 4px;
  line-height: 1.5rem;
  width: fit-content;
  align-self: flex-end;
  margin: auto 3rem 5rem 0;
}

.header {
  display: flex;
  position: relative;
  margin: 0;
}

.header h2 {
  font-size: 1rem;
  line-height: 1rem;
  font-weight: 400;
  color: #FEFFB3;
  text-align: center;
  width: 100%;
  padding: .5rem 0;
  display: none;
}

@media (max-width: 768px) {
  .background {
    display: none;
  }

  .img {
    margin-right: 1rem;
    max-width: 5rem;
    max-height: 6rem;
  }

  .cart-container {
    height: 100vh;
    width: 100vw;
    min-width: 0;
    padding: 0 1rem;
    margin: 0;
    gap: 1rem;
  }

  .cart-wrapper {
    transition: none;
    width: 100vw;
    height: 100vh;
  }

  .catalog-btn {
    border: 1px solid #fbe0f3;
    padding: .75rem 1rem .75rem 1rem;
    margin: 2.5rem auto 0 auto;
    font-size: 1rem;
    line-height: 1rem;
    letter-spacing: 2px;
  }

  .cart-nothing-title {
    font-size: 1.5rem;
    line-height: 1;
    text-align: center;
  }

  .nothing-paragraph {
    font-size: 1rem;
    line-height: 1rem;
  }

  .paragraph-mobile, .header h2 {
    display: inline-block;
    text-align: center;
  }

  .paragraph-pc {
    display: none;
  }

  .cart-nothing-container {
    margin: 0 1rem;
    gap: .5rem;
  }

  .exit-btn {
    cursor: pointer;
    margin: 0;
    position: absolute;
    right: 0;
    top: 0;
    margin-right: .25rem;
  }

  .exit-btn svg {
    width: 1.25rem;
    height: 1.25rem;
  }

  .header {
    margin-top: 2rem;
    margin-bottom: 1rem;
  }

  .header h2 {
    font-size: 1.25rem;
  }

  .card-container {
    min-width: 0;
    margin: 0;
    padding: .5rem;
  }

  .title {
    font-size: 1rem;
    font-weight: 300;
    letter-spacing: 2px;
    line-height: 1rem;
  }

  .price {
    font-size: 1rem;
    font-weight: 300;
    line-height: 1rem;
    letter-spacing: 2px;
  }

  .last-price-container, .last-price   {
    font-size: 1rem;
    line-height: 1rem;
    font-weight: 300;
    letter-spacing: 2px;
    text-wrap: nowrap;
    margin: 0;
  }

  .buy-btn svg {
    max-width: .5rem;
    max-height: .5rem;
  }

  .buy-btn {
    gap: .5rem;
    font-size: 1rem;
    padding: .625rem 3.25rem;
  }

  .buy-container {
    margin: auto 0 1rem 0;
  }
}