.container {
  padding: .5 3rem .5 3rem;
  max-width: 1920px;
  width: 100%;
  margin: 0 auto;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  scroll-margin-top: 100px;
  padding: 0 3rem;
}

.header__title {
  color: #feffb3;
  font-weight: 400;
  font-size: 2rem;
  line-height: 2.375rem;
  padding: 1rem 0 1rem 0;
  cursor: pointer;
}

.header__nav-list {
  display: flex;
  gap: 4rem;
}

.header__nav-link {
  font-size: 1rem;
  line-height: 1rem;
  font-weight: 400;
  letter-spacing: 4px;
  transition: all 0.25s ease-in-out;
  color: #fbe0f3;
  text-align: center;
  align-self: center;
}

.header__nav-link:hover,
.header__nav-link:active {
  color: #e18186;
}

.header__social {
  display: flex;
}

.header__utils {
  display: flex;
  box-sizing: border-box;
  justify-content: end;
}

.header__utils-user {
  cursor: pointer;
  height: 2rem;
  width: 2rem;
  transition: all 0.2s ease-in-out;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__utils-user svg {
  width: 100%;
  height: 100%;
}

.header__utils-user:hover {
  color: rgba(251, 224, 243, 0.7);
}

.header__utils-cart {
  width: 2rem;
  height: 2rem;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  color: #fbe0f3;
  margin-right: 1.5rem;
  position: relative;
}

.header__utils-cart svg {
  width: 100%;
  height: 100%;
}

.header__utils-cart:hover {
  color: rgba(251, 224, 243, 0.7);
}

.header__utils-cart:hover .cart-list {
  background: rgba(251, 224, 243, 0.7);;
}

.header__utils-close {
  max-width: 28px;
  max-height: 28px;
  transition: all 0.2s ease-in-out;
}

.header__utils-close:hover {
  color: rgba(251, 224, 243, 0.7);
}

.cart-list {
  position: absolute;
  font-size: 1rem;
  font-weight: 400;
  line-height: .5rem;
  color: #222023;
  background: #FBE0F3;
  padding: .5rem;
  max-width: 1.5rem;
  width: auto;
  text-align: center;
  border-radius: 50%;
  bottom: -30%;
  right: -30%;
  transition: background-color .2s ease-in;
}

.burger_menu_btn {
  background: none;
  border: 0;
  padding: 0;
  margin: 0;
  outline: none;
  cursor: pointer;
  max-width: 1.5rem;
  max-height: auto;
  width: 100%;
  height: auto;
  display: none;
}

@media (max-width: 768px) {
  .header {
    padding: 0 1rem;
  }
  .header__nav {
    display:none;
  }

  .header__title {
    font-size: 1.25rem;
    line-height: 1;
    padding: 0;
    margin-top: 0;
    position: absolute;
    transform: translateX(50%);
    right: 50%;
  }

  .header__utils svg {
    max-width: 1.25rem;
    width: 100%;
    height: auto;
  }

  .header__utils {
    gap: .75rem;
  }

  .header__utils-cart, .header__utils-user {
    max-width: 1.5rem;
    max-height: 1.5rem;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: .25rem 0;
  }

  .cart-list {
    padding: 0;
    font-size: .5rem;
    height: 10px;
    width: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    bottom: -5%;
    right: -5%;
  }

  .header {
    margin-top: 2rem;
  }

  .burger_menu_btn {
    display: block;
  }
}