.wrapper {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    right: 0;
    display: flex;
    justify-content: flex-start;
    transform: translateX(-100%);
    transition: transform .3s ease-out;
    z-index: 999;
}

.blackout {
    z-index: 998;
    position: fixed;
    top: 0;
    right: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(16, 16, 16, 0.8);
    transition: opacity .3s ease-out, visibility .3s ease-out;
    opacity: 1;
    visibility: visible;
}

.open {
    transform: translateX(0);
}

.closeBlackout {
    opacity: 0;
    visibility: hidden;
}


.container {
    width: 70vw;
    height: 100%;
    background-image: url("/public/images/pattern.webp");
    background-repeat: repeat;
    background-size: 600px 600px;
    image-rendering: pixelated;
    z-index: 999;
    padding: 1.5rem 1rem;
    display: flex;
    flex-direction: column;
}

.title {
    font-size: 1.25rem;
    font-weight: 400;
    color: rgba(254, 255, 179, 1);
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
}

.header button {
    width: 2rem;
    height: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: none;
    outline: none;
    border: none;
    margin: 0;
    cursor: pointer;
    padding: 0;
}

.navigation {
    font-size: 1rem;
    line-height: 1.25rem;
    font-weight: 400;
    color: #FBE0F3;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    width: fit-content;
}

.navigation button {
    background: none;
    outline: none;
    margin: 0;
    padding: 0;
    border: 0;
    color: #FBE0F3;
    text-align: start;
}

.exit-btn {
    margin-top: auto;
    border: none;
    outline: none;
    background: none;
    color: rgba(254, 255, 179, 0.5);
    text-align: start;
}