.lk__wrapper {
  max-width: 1905px;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

.lk__inside-wrapper {
  padding: 0 3rem 0 3rem;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.lk__title {
  text-align: start;
  margin-top: 5rem;
  color: #feffb3;
  font-size: 3.5rem;
  line-height: 4rem;
  font-weight: 400;
}

.lk__paragraph {
  margin-top: 1rem;
  margin-bottom: 5rem;
  font-size: 1.5rem;
  line-height: 1.5rem;
  font-weight: 300;
  color: rgba(251, 224, 243, 0.8);
  letter-spacing: 4px;
}

.lk__my-trainig {
  color: #feffb3;
  font-size: 3.5rem;
  line-height: 4rem;
  font-weight: 400;
  margin-bottom: 2.5rem;
}

.lk__trainings {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: space-between;
  row-gap: 4rem;
  margin-bottom: 6rem;
}

.lk__trainings-element:nth-child(odd) {
  justify-self: start;
}

.lk__trainings-element:nth-child(even) {
  justify-self: end;
}

.lk__trainings-element {
  display: flex;
  border: 2px solid #fbe0f3;
  max-width: 52.5rem;
  width: 100%;
}

.lk__trainings-element img {
  padding: 1rem 1.5rem 1rem 1rem;
}

.lk__trainings-element-wrapper {
  padding: 1.5rem 1.5rem 1.5rem 0;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.lk__trainings-element-second-wrapper {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.lk__trainings-element-second-wrapper p {
  font-size: 2rem;
  color: #fbe0f3;
  font-weight: 400;
  line-height: 2rem;
}

.lk__trainings-element-time {
  text-align: end;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.lk__trainings-element-time p {
  font-size: 1rem;
  line-height: 1rem;
  font-weight: 400;
  color: #feffb3;
  letter-spacing: 2px;
  text-decoration: underline;
}

.lk_trainings-element-extend-btn {
  font-size: 1rem;
  line-height: 1rem;
  font-weight: 400;
  letter-spacing: 2px;
  color: rgba(251, 224, 243, 0.5);
  text-align: end;
  border: none;
  padding: 0;
  margin: 0;
  outline: none;
  background: transparent;
  text-decoration: underline;
  white-space: nowrap;
  cursor: pointer;
}

.lk__trainings-element-proceed-btn {
  background: transparent;
  outline: none;
  border: 0;
  padding: 0;
  color: #feffb3;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 1.5rem;
  letter-spacing: 4px;
  text-decoration: underline;
  align-self: flex-end;
  margin-top: auto;
  cursor: pointer;
}

.exit_button {
  outline: none;
  border: 0;
  margin: 5rem 0 0 0;
  padding: 0;
  background: transparent;
  color: rgba(254, 255, 179, 0.5);
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1.5rem;
  letter-spacing: 4px;
  cursor: pointer;
  transition: all .3s ease-out;
}

.exit_button:hover, .exit_button:focus {
  color: #feffb3;
}

.lk__utils {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.empty_cart h2 {
  font-size: 2rem;
  font-weight: 400;
  line-height: 2rem;
  color: #FBE0F3;
  text-align: start;
  margin-bottom: 1rem;
}

.empty_cart {
  display: flex;
  flex-direction: column;
}

.empty_cart p {
  font-size: 1.5rem;
  font-weight: 400;
  letter-spacing: 4px;
  line-height: 1.5rem;
  color: #FBE0F3;
  text-align: start;
}

.empty_cart button {
  background: transparent;
  border: 2px solid #FBE0F3;
  border-radius: 100rem;
  color: #FBE0F3;
  padding: 1rem 9rem;
  font-size: 1.5rem;
  line-height: 1.5rem;
  margin: 5rem auto;
  cursor: pointer;
  letter-spacing: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  text-wrap: nowrap;
}

@media (max-width: 768px) {
  .exit_button {
    display: none;
  }

  .lk__inside-wrapper {
    padding: 0 1rem 0 1rem;
  }

  .lk__title {
    text-align: start;
    margin-top: 5rem;
    font-size: 1.5rem;
    line-height: 1.5rem;
    font-weight: 400;
    margin-top: 2rem;
  }

  .lk__paragraph {
    font-size: .75rem;
    line-height: .75rem;
    letter-spacing: 2px;
    margin: 1rem 0 2rem 0;
  }
  
  .lk__my-trainig {
    font-size: 1.5rem;
    line-height: 1.5rem;
    margin-bottom: 2rem;
  }

  .lk__trainings-element {
    max-width: 90vw;
    position: relative;
    border: 1px solid #fbe0f3;
    justify-self: start !important;
  }

  .lk__trainings {
    grid-template-columns: 1fr;
    gap: 6rem;
    margin-top: 3rem;
  }

  .lk__trainings-element img {
    padding: .5rem 1rem .5rem .5rem;
    display: block;
    height: auto;
    object-fit: cover;
    width: 6rem;
  }

  .lk__trainings-element-second-wrapper p {
    font-size: 1rem;
    font-weight: 300;
    line-height: 1rem;
  }

  .lk__trainings-element-proceed-btn {
    font-size: 1rem;
    line-height: 1rem;
    font-weight: 300;
    text-wrap: nowrap;
    letter-spacing: 2px;
  }

  .lk__trainings-element-time {
    position: absolute;
    bottom: 110%;
    right: 0;
    gap: .75rem;
  }

  .lk__trainings-element-wrapper {
    padding: .75rem 1rem .75rem 0;
  }

  .empty_cart h2 {
    font-size: 1.5rem;
    text-align: center;
  }

  .empty_cart p {
    font-size: .75rem;
    line-height: 1;
    text-align: center;
  }

  .empty_cart button {
    font-size: .75rem;
    gap: .5rem;
    padding: .5rem 1.5rem;
    letter-spacing: 2px;
    margin: 2rem auto 2rem auto;
  }

  .empty_cart button svg {
    width: .5rem;
    height: .5rem;
  }
}